// Extra large devices (large desktops, 1200px and up)
@media (min-width: 1200px){
  /* === === === === */
 .container{
   max-width: 1200px !important;
   font-family: 'Play', sans-serif;
 }
 .container.wrap {
   margin: 25px auto;
   -webkit-box-shadow: 2px -2px 10px 1px rgba(0,0,0,1);
   -moz-box-shadow: 2px -2px 10px 1px rgba(0,0,0,1);
   box-shadow: 2px -2px 10px 1px rgba(0,0,0,1);
 }
 /* === === === === */
 h2 {
   font-family: 'Play', sans-serif;
   text-align: center;
   font-size: 2rem;
   letter-spacing: 1px;
   text-transform: uppercase;
   font-weight: 600;
   padding: 0.1em 0;
   background-color:rgb(39, 46, 56, .4);
   color: #fff;
 }
 /* === === === === */
 p {
   font-size: 1.2rem;
   line-height: 2.2rem;
   &.icon-check::before{
     content:'';
     background: url(../images/check-mark-box.svg) no-repeat;
     background-size: 100%;
     margin-right: 16px;
     padding: 14px;
     font-size: 0.6rem;
   }
   &.bg-red{
     background: #9a0007;
   }
 }
 /* === === === === */
 .bg-red{
   background: rgb(154, 0, 7, .7) !important;
   color: #fff;
 }
 /* === === === === */
 .bg-blue{
   background: rgb(39, 46, 56, .7) !important;
   color: #fff;
 }
 /* === === === === */
 .bottom{
   position: absolute;
   left: 0;
   bottom:0;
 }
 /* === === === === */
 .btn{
   color: #fff !important;
   border-radius: 0;
   font-size: 1.2rem;
   padding: 0.4em 1.8em;
   &.btn-danger{
     background: rgb(212,75,56);
     border: 1px solid rgb(212,75,56);
   }
   &.btn-primary{
     background: rgb(39, 46, 56);
     border: 1px solid rgb(39, 46, 56);
   }
   &:hover{
     opacity: .8;
   }
 }
 /* === === === === */
 section {
   overflow: hidden !important;
   position: relative;
   min-height: 820px;
   height: 100vh;
   margin-left: -15px;
   margin-right: -15px;
 }

 section.main{
   overflow: hidden;
   &::before{
     content:'';
     background: url(../images/banner_min.jpg) no-repeat;
     background-size: 100%;
     position: absolute;
     top: 30px;
     left: 0px;
     width: 100%;
     height: 100%;
     z-index: -1;
   }
   & header{
     position: relative;
     width: 100%;
     height: 95px;
     background-color: #fff;
     &::before{
       content:'';
       position: absolute;
       top: 30px;
       left: 50%;
       margin-left: -150px;
       background: url(../images/aspro_min.png) no-repeat;
       background-size: 100%;
       width: 300px;
       height: 100px;
       z-index: 3;
     }
   }

   & .triangles-upper-lower{
     &::before{
       content:'';
       position: absolute;
       top: 95px;
       right: 0px;
       border-right: 992px solid #fff;
       border-top: 0px solid transparent;
       border-bottom: 240px solid transparent;
       z-index: 2;
     }
     &::after{
       content:'';
       position: absolute;
       top: 415px;
       left: 0px;
       border-left: 992px solid #fff;
       border-top: 250px solid transparent;
       border-bottom: 0px solid transparent;
       z-index: 2;
     }
   }
 
   & .triangles-right-left{
     &::before{
       content:'';
       position: absolute;
       top: 95px;
       right: 0px;
       border-right: 460px solid #9a0007;
       border-top: 120px solid transparent;
       border-bottom: 112px solid transparent;
       z-index: 3;
     }
     &::after{
       content:'';
       position: absolute;
       top: 263px;
       left: 0px;
       border-left: 560px solid #272e38;
       border-top: 142px solid transparent;
       border-bottom: 315px solid transparent;
       z-index: 3;
     }
   }
 
   & .lines-long-short{
     &::before{
       content:'';
       position: absolute;
       right: 0;
       top: 594px;
       width: 625px;
       transform: skewY(14deg);
       border-bottom: 25px solid #9a0007;
       /* угол на конце линии*/
       border-left: 32px solid transparent;
       z-index: 2;
     }
     &::after{
       content:'';
       position: absolute;
       right: 0;
       top: 646px;
       width: 460px;
       transform: skewY(14deg);
       border-bottom: 18px solid #272e38;
       /* угол на конце линии*/
       border-left: 20px solid transparent;
       z-index: 2;
     }
   }
 
   & .text{
     font-family: 'Play', sans-serif;
     position: absolute;
     top: 685px;
     left: 145px;
     width: 540px;
     font-size: 1.1rem;
     z-index: 3;
   }
 
   & .contact{
     position: absolute;
     font-family: 'Play', sans-serif;
     font-size: 18px;
     color: #fff;
     top: 360px;
     left: 40px;
     z-index: 4;
     h1{
       font-weight: 700;
       transform: skew(-15deg, 0deg);
       margin-bottom: 6px;
     }
     & .address{
       display: block;
       margin-bottom: 60px;
     }
   
     & a{
       display: block;
       color: #fff;
       text-decoration: none;
       cursor: pointer;
       margin-bottom: 14px;
       margin-left: 20px;
     }
   
     & a.phone::before {
       content: "";
       background: url(../images/phone.svg) no-repeat;
       background-size: 100%;
       padding: 20px 22px 0 0;
     }
   
     & a.email::before {
       content: "";
       background: url(../images/email.svg) no-repeat;
       background-size: 100%;
       padding: 0px 22px 0 0;
     }
   
     & a span {
       margin-left: 22px;
     }
   }
   & .menu{
     position: absolute;
     top: 155px;
     right: 0;
     width: 190px;
     text-transform: uppercase; 
     font-family: 'Open Sans', sans-serif;
     font-size: 14px;
     z-index: 100;
     & ul {}
     & li {
       list-style-type: none;
       padding-bottom: 10px;
       font-weight: 600;
     }
     & li::before {
       //content: "";
     }
   
     & a {
       font-family: 'Play', sans-serif;
       color: #fff;
      // text-decoration: none;
       cursor: pointer;
       &:hover{
         font-weight: 700;
       }
     }
   }

 }

 section.about{
   &::before{
     content:'';
     background: url(../images/men-min.png) no-repeat;
     background-size: 100%;
     position: absolute;
     top: 35px;
     left: -20px;
     width: 680px;
     height: 100%;
     z-index: -1;
   }
 }

 section.customers{
   .carousel-inner img{
     height: 560px !important;
   }
 }

 section.suppliers{
   position: relative;
   &::before{
     content:'';
     position: absolute;
     background:url(../images/cap-min.png) no-repeat;
     background-size: 100%;
     top: 50%;
     left: 50%;
     margin-left: -250px;
     margin-top: -300px;
     width: 500px;
     height: 600px;
     z-index: -1;
   }

   .img-thumbnail.mini{
     width: 150px;
   }
 }

 section.contacts{
   .holder{
     min-height: 450px;
   }

   & .image{
     background: url(../images/znak.png) no-repeat center; 
     background-size: 100%;
   }
   & .form{
     display: none;
     & input, textarea{
       border-radius: 0;
       font-size: 1.2rem;
     }
   }
   & .requisites{
     & ul {
       list-style-type: none;
     }
     & li{
      // margin-left: -15px;
       font-size: 1.2rem;
     }
   }
   & .btns{
     position: absolute;
     left: 0;
     bottom: 0;
     width: 100%;
   }
 }
}
